@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:opsz,wght@6..12,200&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Nunito Sans', sans-serif;
}

html, body, #root {
  width: 100%;
  min-height: 100%;
  background: rgb(38,196,165);
  background: linear-gradient(0deg, rgba(38,196,165,1) 0%, rgba(84,217,159,1) 50%, rgba(172,224,140,1) 100%);
}